<template>
  <div class="app-container" v-loading="allLoading" :class="{'ly-is-full':isFull}">
    <div class="filter-container" ref="tableSelect">

      <el-row class="saltminion_global" style="margin-top: 5px">
        <el-tooltip class="item" effect="dark" content="更新所有minion的grains数据，比较耗时" placement="bottom-start">
          <el-button  type="primary"  @click="handleUpdateMinionList">更新列表</el-button>
        </el-tooltip>
        <el-tooltip effect="dark" content="只更新所有minion的状态，速度较快" placement="bottom-start">
          <el-button   type="primary"  @click="handleUpdateMinionStatus">更新状态</el-button>
        </el-tooltip>
        <el-tooltip effect="dark" content="同步saltkey管理中的删除操作" placement="bottom-start">
          <el-button   type="danger" size="default" @click="handleDeleteKeyList">批量删除</el-button>
        </el-tooltip>
        <div style="margin-left: auto">
          <el-input placeholder="Minion_ID" v-model="listQuery.minion_id" style="width: 150px;"  @keyup.enter.native="handleFilter"/>
          <el-input placeholder="IP" v-model="listQuery.ip" style="width: 150px;margin-left: 10px" @keyup.enter.native="handleFilter"/>
          <el-select v-model="listQuery.sys" clearable style="width: 140px;margin-left: 10px" placeholder="系统类型" @change="handleFilter">
            <el-option label="Linux" value="Linux"/>
            <el-option label="Windows" value="Windows"/>
          </el-select>
          <el-select v-model="listQuery.minion_status" clearable style="width: 140px;margin-left: 10px" placeholder="状态" @change="handleFilter">
            <el-option label="在线" value="在线"/>
            <el-option label="离线" value="离线"/>
            <el-option label="异常" value="异常"/>
          </el-select>
          <el-button type="primary" size="default" :icon="Search" @click="handleFilter" style="margin-left: 10px">搜索</el-button>
        </div>
      </el-row>
    </div>

    <el-table
        :height="'calc('+(tableHeight)+'px)'"
        v-loading="listLoading"
        :key="tableKey"
        :data="data_list"
        border
        fit
        highlight-current-row
        style="width: 100%;"
        @sort-change="sortChange"
        @selection-change="handleSelectionChange"
        class="saltminion_table">
      <el-table-column type="selection" align="center" width="55">
      </el-table-column>
      <el-table-column label="MinionID" prop="minion_id" sortable="custom"  align="center" width="200">
        <template #default="scope">
          <span>{{ scope.row.minion_id }}</span>
        </template>
      </el-table-column>
      <el-table-column label="IP地址" prop="ip" sortable="custom"  align="center" width="200">
        <template #default="scope">
          <span>{{ scope.row.ip }}</span>
        </template>
      </el-table-column>
      <el-table-column label="域名" prop="do_main" sortable="custom"  align="center" width="200">
        <template #default="scope">
          <span>{{ scope.row.do_main }}</span>
        </template>
      </el-table-column>
      <el-table-column label="系统类型"  align="center" width="100">
        <template #default="scope">
          <span>{{ scope.row.sys }}</span>
        </template>
      </el-table-column>
      <el-table-column label="Minion版本"  align="center" width="150">
        <template #default="scope">
          <span>{{ scope.row.minion_version }}</span>
        </template>
      </el-table-column>
      <el-table-column label="最近一次更新时间" width="200" align="center">
        <template #default="scope">
          <span>{{ scope.row.update_time }}</span>
        </template>
      </el-table-column>
      <el-table-column label="状态" width="100" align="center">
        <template #default="scope">
          <span v-if="scope.row.minion_status === '在线'" class="status_success">在线</span>
          <span v-else-if="scope.row.minion_status === '离线'" class="status_danger">离线</span>
          <span v-else class="status_waring">异常</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" min-width="230" class-name="small-padding fixed-width">
        <template #default="scope">
          <el-button type="primary" size="small" @click="handleUpdateMinion(scope.row.minion_id)">更新</el-button>
          <el-button type="primary" size="small" @click="handleMinionInfo(scope.row)">详情</el-button>
          <el-button type="danger" size="small" @click="handleDeleteKey(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <NewPagination v-bind:child-msg="listQuery" @pagination="getPagination"></NewPagination>

    <el-dialog :title="`MinionID ${temp.minion_id} 详细信息`" v-model="dialogFormVisible" >
      <el-scrollbar style="height:100%">
        <el-form ref="dataForm"  :model="temp" label-position="right" label-width="130px" style="width: 800px;">
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="MinionID" prop="minion_id">
                <el-input readonly v-model="temp.minion_id"/>
              </el-form-item>
              <el-form-item label="IP" prop="ip">
                <el-input autosize readonly v-model="temp.ip"/>
              </el-form-item>
              <el-form-item label="域名" prop="do_main">
                <el-input autosize readonly v-model="temp.do_main"/>
              </el-form-item>
              <el-form-item label="Minion版本" prop="minion_version">
                <el-input readonly v-model="temp.minion_version"/>
              </el-form-item>
              <el-form-item label="系统版本" prop="system_issue">
                <el-input readonly v-model="temp.system_issue"/>
              </el-form-item>
              <el-form-item label="SN序列号" prop="sn">
                <el-input type="textarea" autosize readonly v-model="temp.sn"/>
              </el-form-item>
              <el-form-item label="CPU个数" prop="cpu_num">
                <el-input readonly v-model="temp.cpu_num"/>
              </el-form-item>
              <el-form-item label="CPU型号" prop="cpu_model">
                <el-input readonly v-model="temp.cpu_model"/>
              </el-form-item>
              <el-form-item label="系统类型" prop="sys">
                <el-input readonly v-model="temp.sys"/>
              </el-form-item>
              <el-form-item label="内核版本" prop="kernel">
                <el-input readonly v-model="temp.kernel"/>
              </el-form-item>
              <el-form-item label="品牌名称" prop="product_name">
                <el-input readonly v-model="temp.product_name"/>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="IPv4地址" prop="ipv4_address">
                <el-input type="textarea" autosize readonly v-model="temp.ipv4_address"/>
              </el-form-item>
              <el-form-item label="MAC地址" prop="mac_address">
                <el-input type="textarea" autosize readonly v-model="temp.mac_address"/>
              </el-form-item>
              <el-form-item label="主机名" prop="localhost">
                <el-input readonly v-model="temp.localhost"/>
              </el-form-item>
              <el-form-item label="内存大小(M)" prop="mem_total">
                <el-input readonly v-model="temp.mem_total"/>
              </el-form-item>
              <el-form-item label="创建时间" prop="create_date">
                <el-date-picker readonly v-model="temp.create_date" type="datetime"/>
              </el-form-item>
              <el-form-item label="最近一次更新时间" prop="update_time">
                <el-date-picker readonly v-model="temp.update_time" type="datetime"/>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-scrollbar>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script setup>
import {nextTick, onBeforeMount, onMounted, ref} from "vue";
import {Search} from "@element-plus/icons-vue";
import { ElMessage, ElMessageBox } from 'element-plus'
import { getSaltMinionList, updateMinionList, updateMinionStatus, updateMinion, deleteSaltKey} from "@/api/api";
import NewPagination from "@/components/pagination_new/NewPagination.vue";
import {getTableHeight} from "@/utils/util";

let isFull = ref(false)
let tableHeight = ref(500);
let tableSelect = ref(null);
let allLoading = ref(false);
let listLoading = ref(true);
let data_list = ref([]);
let total = ref(0);
let tableKey = ref(0);
let listQuery = ref({
  page: 1,
  page_size: 10,
  total: total.value,
  minion_id:'',
  ip:'',
  sys:'',
  minion_status:'',
  ordering: ''
});
let multipleSelection = ref([]);
let dialogFormVisible = ref(false);
let temp = ref({
  minion_id: '',
  ip: '',
  do_main: '',
  minion_version: '',
  system_issue: '',
  sn: '',
  cpu_num: '',
  cpu_model: '',
  sys: '',
  kernel: '',
  product_name: '',
  ipv4_address: '',
  mac_address: '',
  localhost: '',
  mem_total: '',
  create_date: new Date(),
  update_time: new Date(),
  minion_status: '',
  description: '',
})

onMounted(() => {
  getList()
})

function getPagination(parm) {
  listQuery.value.page = parm.page
  listQuery.value.limit = parm.limit
  getList()
}

// 获取minion列表
function getList() {
  listLoading.value = true
  getSaltMinionList(listQuery.value).then(response => {
    data_list.value = response.data.data
    total.value = response.data.total
    listQuery.value.total = response.data.total
    setTimeout(() => {
      listLoading.value = false
    }, 0.5 * 1000)
  }).catch(error => {
    console.log('获取minion列表' + error);
  })
}
// 获取minion列表不需要loading的
function getListNoLoading() {
  getSaltMinionList(listQuery.value).then(response => {
    data_list.value = response.data.data
    total.value = response.data.total
    listQuery.value.total = response.data.total
  }).catch(error => {
    console.log('获取minion列表不需要loading的' + error);
  })
}
// 搜索框操作
function handleFilter() {
  listQuery.value.page = 1
  getList()
}
// 更新列表
function handleUpdateMinionList(){
  ElMessageBox.confirm('此操作为耗时操作将获取所有minion的状态及grains信息, 是否继续?', '提示', {
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    type: 'warning'
  }).then(() => {
    allLoading.value = true;
    let data = {
      "salt_minion_tag": "global_update_salt_minion_list"
    }
    updateMinionList(data).then(response => {
      if(response.status){
        ElMessage.success(response.results)
        getListNoLoading()
        allLoading.value = false
      }
      else{
        ElMessage.error(response.data.results)
        allLoading.value = false
      }
    }).catch(error => {
      console.log('更新列表' + error);
    })
  }).catch(() => {
    ElMessage.info({
      message: '操作取消'
    });
  });
}
// 更新状态
function handleUpdateMinionStatus(){
  allLoading.value = true
  let data = {
    "salt_minion_tag": "global_update_salt_minion_status"
  }
  updateMinionStatus(data).then(response => {
    if(response.status){
      ElMessage.success(response.results)
      getListNoLoading()
      allLoading.value = false
    }
    else{
      ElMessage.error(response.results)
      allLoading.value = false
    }
  }).catch(error => {
    console.log('更新状态' + error);
  })
}
// 更新单个minion
function handleUpdateMinion(minion_id) {
  listLoading.value = true
  let data = {
    "salt_minion_tag": "update_salt_minion",
    "minion_id":minion_id
  }
  updateMinion(data).then(response => {
    if(response.status){
      ElMessage.success(response.results)
      getList()
      listLoading.value = false
    }
    else{
      ElMessage.error(response.results)
      listLoading.value = false
    }
  }).catch(error => {
    console.log('更新单个minion' + error);
  })
}
// 删除key基类方法
function deleteKey(minion_id){
  let data = {
    "salt_key_tag": "delete_salt_key",
    "minion_id": minion_id
  }
  deleteSaltKey(data).then(response => {
    if(response.status){
      ElMessage.success(response.results)
      getList()
    }
    else{
      ElMessage.error(response.results)
    }
  }).catch(error => {
    console.log('删除key基类方法' + error);
  })
}
// 删除key基类方法调用不需要loading的
function deleteKeyNoLoading(minion_id){
  let data = {
    "salt_key_tag": "delete_salt_key",
    "minion_id": minion_id
  }
  deleteSaltKey(data).then(response => {
    if(response.status){
      ElMessage.success(response.results)
      allLoading.value = false
      getListNoLoading()
    }
    else{
      ElMessage.error(response.results)
      allLoading.value = false
    }
  }).catch(error => {
    console.log('删除key基类方法调用不需要loading的' + error);
  })
}
// 删除单个key
function handleDeleteKey(row) {
  ElMessageBox.confirm('确认要删除?', '提示', {
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    type: 'warning'
  }).then(() => {
    let minion_id = [row.minion_id]
    deleteKey(minion_id)
  }).catch(() => {
    ElMessage.info({
      message: '已取消删除'
    });
  })
}
// 批量删除key
function handleDeleteKeyList(){
  if(multipleSelection.value.length === 0){
    ElMessage.error('未选择任何记录，请选择后重试')
  }
  else{
    ElMessageBox.confirm('确认要删除?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      allLoading.value = true
      let minion_id = []
      for(let data of multipleSelection.value.values()){
        minion_id.push(data.minion_id)
      }
      deleteKeyNoLoading(minion_id)
    }).catch(() => {
      ElMessage.info({
        message: '已取消删除'
      });
    });
  }
}
// 当选项发生变化时触发该方法多选赋值,val是@selection-change内置的表示选中的对象
function handleSelectionChange(val) {
  multipleSelection.value = val;
}
// 排序
function sortChange(data) {
  const { prop, order } = data
  if (prop === 'minion_id') {
    if (order === 'ascending') {
      listQuery.value.ordering = 'minion_id'
    } else {
      listQuery.value.ordering = '-minion_id'
    }
  }
  else if(prop === 'ip'){
    if (order === 'ascending') {
      listQuery.value.ordering = 'ip'
    } else {
      listQuery.value.ordering = '-ip'
    }
  }
  handleFilter()
}
function resetTemp() {
  temp.value = {
    minion_id: '',
    ip: '',
    minion_version: '',
    system_issue: '',
    sn: '',
    cpu_num: '',
    cpu_model: '',
    sys: '',
    kernel: '',
    product_name: '',
    ipv4_address: '',
    mac_address: '',
    localhost: '',
    mem_total: '',
    create_date: new Date(),
    update_time: new Date(),
    minion_status: '',
    description: '',
  }
}
function handleMinionInfo(row) {
  resetTemp()
  temp.value = Object.assign({}, row) // copy obj
  temp.value.create_date = new Date(temp.value.create_date)
  temp.value.update_time = new Date(temp.value.update_time)
  dialogFormVisible.value = true
}
function getTheTableHeight() {
  let tabSelectHeight = tableSelect.value ? tableSelect.value.offsetHeight:0
  console.log(tabSelectHeight, "1")
  tabSelectHeight = isFull.value?tabSelectHeight-110:tabSelectHeight
  console.log(tabSelectHeight, "2")
  tableHeight.value = getTableHeight(tabSelectHeight)
  console.log(tableHeight.value, "3")
}
function listenResize() {
  nextTick(() => {
    getTheTableHeight()
  })
}
onMounted(() => {
  window.addEventListener('resize', listenResize)
  nextTick(() => {
    console.log("onMounted")
    getTheTableHeight()
  })
})
onBeforeMount(() => {
  window.removeEventListener("resize", listenResize);
})
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.app-container{
  .saltminion_table{
    .status_success {
      color: #40c984;
    }
    .status_danger {
      color: #f4516c;
    }
    .status_waring {
      color: #f4ce51;
    }
  }

}
</style>